const onlyEnglish =val=> /^[a-zA-Z\s]*$/.test(val);
const checkAlphabet = (val) => 	/[a-zA-Z0-9_]/i.test(val);
const checkStringName = val => {

	if (val && val.split(/\W+/).length === 2) {
		return true;
	 }
	 return false;
}
const idNumber = (val)=> {
	var res = String(val).replace(/_/g, "");
    if(res.length !== 9) {
        return false
    }else {
        return true
    }
}


const isExpDateOfCardValid = (input) =>{
	var expDate = String(input).toUpperCase().replace(/[^0-9]/g, '');
	var date = expDate.match(/^(\d{2})(\d{2})$/);

	if(expDate.length === 4){
		if(date[1] > 12 || date[2] < 20){
			return false;
		}else {
			return true
		}

	}else{
		return false
	}
}

const checkCardValid= (value) => {
	if (/[^0-9-\s]+/.test(value)) return false;

	let nCheck = 0, nDigit = 0, bEven = false;
	value = value.replace(/\D/g, "");

	if(value.length !== 16){
		return false;
	}else{
		for (let n = value.length - 1; n >= 0; n--) {
			let cDigit = value.charAt(n),
				nDigit = parseInt(cDigit, 10);

			if (bEven) {
				if ((nDigit *= 2) > 9) nDigit -= 9;
			}

			nCheck += nDigit;
			bEven = !bEven;
		}
		if((nCheck % 10) !== 0){
			return false;
		}else{
			return true;
		}
	}
}


function todayDate() {
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); 
    var yyyy = today.getFullYear();
    let date=yyyy+'-'+mm+'-'+dd;
    return date;
}
const dateToday = todayDate();

const issueDateCardId = (val) => {
	if (val) {
	var year = val.substring(0,4);
    var todayYear = dateToday.substring(0,4);
    var month = parseInt(val.substring(5,7));
    var todayMonth = parseInt(dateToday.substring(5,7));
    var day = parseInt(val.substring(8,12));
    var todayDay = parseInt(val.substring(8,12));
	

	if(year>todayYear) {
        return false;
	}
	if(year < todayYear-11) {
		return false;
	}
	if(year===todayYear && month>todayMonth) {
		return false
	}
	if(year===todayYear && month===todayMonth && day>todayDay) {
		return false;
	}
	
	return true;
	}
}
const  expDate = (val)=>  {
    if (val && val === "__/__" && val.length=== 5) return false;
    if(val && val.length !== 5) return false;
    return true
}

