import React, { Component } from 'react';
import { MDBContainer, MDBBtn, MDBModal, MDBModalBody, MDBModalHeader, MDBModalFooter } from 'mdbreact';
import { NavLink, withRouter } from 'react-router-dom';
class SuccessSubmit extends Component {
state = {
  modal14: false
}

toggle = nr => () => {
  let modalNumber = 'modal' + nr
  this.setState({
    [modalNumber]: !this.state[modalNumber]
  });
}

render() {
  return (
      <MDBContainer>
        <MDBModal isOpen="true" toggle={this.toggle(14)} centered>
          <MDBModalBody className="successMessage text-center text-success">
          <p className="mb-5">Ваша заявка принята в обработку.<br></br> Наш менеджер свяжется с вами ближайшее время<br></br></p> 

           
          <NavLink className="tohome"  to="/home">Вернуться на главную сайта</NavLink>
          </MDBModalBody>
        
        </MDBModal>
      </MDBContainer>
    );
  }
}

export default SuccessSubmit;