import React, {Component} from 'react';
import './App.css';
import App from './App';
import { Switch, Route, Redirect, withRouter,IndexRoute } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from "react-transition-group";
import styled from "styled-components";
import Nav from './defaults/Nav';
import Footer from './defaults/Footer';
import Home from './components/Home';
import Contacts from './components/Contacts';
import Payment from './components/Payment';
import Instruction from './components/Instruction';
import Questans from './components/Questionans';
import Stepmoney from './components/Stepmoney';
import Conditions from './components/Conditions';
import ProgressBar from './components/ProgressBar';
import Success from './components/SuccessSubmit'
import ReactRouterLoader from 'react-router-loader';
import StepForm from './FormToRegister/FormStep';
import Complaint from './components/Complaint';
import history from './components/history'
import { connect } from 'react-redux';
import MetaInfo from './defaults/metaHeadInfo';
import {postRegistration }from './redux/actions/ActionCreators';
import $ from "jquery";

const mapStateToProps = () => {

}
const mapDispatchToProps = (dispatch) => ({ 
  postRegistration:(registration) => dispatch(postRegistration(registration)),
})

const Wrapper = styled.div`
  .fade-enter {
    opacity: 0.01;
  }

  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 3000ms ease-in;
  }

  .fade-exit {
    opacity: 1;
  }

  .fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: opacity 3000ms ease-in;
  }

  div.transition-group {
    position: relative;
  }

  section.route-section {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }
`;

class MainComponent extends Component {

  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className="App">
       <Nav />
              <Switch >
                <Route exact path="/" component={Home}/>
                <Route path = '/payment' component={Payment} />
                <Route path = '/conditions' component={Conditions} />
                <Route path = '/stepmoney' component={Stepmoney} />
                <Route path = '/contacts' component={Contacts} />
                <Route path = '/vopros-otvet' component={Questans} />
                <Route path ="/sposobi_pogashenia" component={Instruction} />
                <Route path = "/get_money" component={() => <StepForm postRegistration = {this.props.postRegistration}/>} />
                <Route path="/complaint" component={Complaint} />
                <Redirect to='/' /> 
              </Switch>
              
        <Footer />
      </div>
    );
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MainComponent));
