import React from 'react'; 
import Stepper from '@material-ui/core/Stepper'
import Step from '@material-ui/core/Step'
import StepLabel from '@material-ui/core/StepLabel'
import FirstStep from './FormRegister';
import CodeConfirm from './CodeConfirm';
import SecondStep from './StepSecond';
import ThirdStep from './ThirdStep';
import {stepRegistration, emptyMessage} from '../redux/actions/ActionCreators'
import { connect } from 'react-redux';
import $ from 'jquery'
import { actions } from 'react-redux-form'
import {Prompt} from 'react-router-dom';
const labels = [ 'Регистрация', 'Заполнения анкеты', 'Отправка анкеты',  ];
const handleSteps = step => {
    switch (step) {
        case 0:
            return (
                <FirstStep
                />
            )
        case 1:
            return (
                <CodeConfirm
                />
               
            )
        case 2:
            return (
                <SecondStep
                />
               
            )
        case 3:
            return (
                <ThirdStep />
            )
        default:
            break
    }
}
const mapStateToProps = state => {
    return {
        stepregistration: state.stepregistration
    }
}
const mapDispatchToProps = (dispatch) => ({
    stepRegistration: step =>{dispatch(stepRegistration(step))},
    resetRegistration: () => { dispatch(actions.reset('registration'))},
    emptyMessage:() => { dispatch(emptyMessage())}
})
const step = () => {
    if((this.state.stepregistration) > 0 ) {
        return this.props.stepregistration - 1
    }
}
class FormStep extends React.Component {

    componentDidMount() {
        $(".registerCyrril").on("keypress", function (event) {

			var englishAlphabetDigitsAndWhiteSpace = /[А-Яа-я ]/g;
	
			var key = String.fromCharCode(event.which);

			if (event.keyCode == 8 || event.keyCode == 37 || event.keyCode == 39 || englishAlphabetDigitsAndWhiteSpace.test(key)) {
				return true;
			}
		
			return false;
		});
		
		$('.registerCyrril').on("paste", function (e) {
			e.preventDefault();
		});
        localStorage.clear();
        this.props.stepRegistration(0);
        // 
        this.props.emptyMessage();
        this.props.resetRegistration();
    }
    render() {
        return (
    <React.Fragment>
				<React.Fragment>
              
					<Stepper className="otherPages" activeStep={parseInt(this.props.stepregistration>0 ? this.props.stepregistration -1 : 0)} style={{ paddingTop: 30, paddingBottom: 50 }} alternativeLabel>
						{labels.map(label => (
							<Step key={label} >
								<StepLabel >{label}</StepLabel>
							</Step>
						))}
					</Stepper>
					{handleSteps(parseInt(this.props.stepregistration))}
				</React.Fragment>
		</React.Fragment>
        )
    }
}

export default (connect(mapStateToProps,mapDispatchToProps)(FormStep));