import React from 'react'
import { connect } from 'react-redux';
import {changingMoney, changingDay} from '../redux/actions/ActionCreators';
import { Modal, ModalHeader, ModalBody, Label,Row } from 'reactstrap';
import { Control, Errors, Form, actions } from 'react-redux-form';
import InputMask from "react-input-mask";
import { postRegistration } from '../redux/actions/ActionCreators';
import ProgressBar from '../components/ProgressBar';
import {iin,required,phoneCheck,validEmail,passwordCheck, acceptCirrilic, confirmPassword} from '../defaults/validations';
import Spinner from 'react-spinner-material';
import $ from 'jquery';
const mapStateToProps = state => {
    return {
      moneyVal: state.moneyVal,
	  dayVal: state.dayVal,
	  checked: false,
	  somemessage: state.message,
	  registration: state.registration,
	  loading: state.loading,
	  registration1: state.registration1
    }
}

const mapDispatchToProps = (dispatch) => ({
    changingMoney: money => { dispatch(changingMoney(money)); },
	changingDay: day => { dispatch(changingDay(day)); },
	postRegistration:(registration) => dispatch(postRegistration(registration)),
	resetRegistration: () => { dispatch(actions.reset('registration'))},

})


											
											
function Latin(obj) {
	   if (/^[a-zA-Z0-9 ,.\-:"()]*?$/.test(obj.value))
		  obj.defaultValue = obj.value;
	   else
		  obj.value = obj.defaultValue;
}
	
const PhoneMask = (props) => <InputMask mask="+7(999)-999-9999" maskChar=" " className="my-input" {...props} />;
const IinMask = (props) => <InputMask mask="999999999999" maskChar=" " className="my-input" {...props} />;
class FormRegister extends React.Component {
	constructor(props) {
        super(props);
        this.state = {
			isModalOpen: false,
			checked: false,
			// phone: "",
			phoneError: "",
        };
		this.toggleModal = this.toggleModal.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleCheck = this.handleCheck.bind(this);
		this.onChange = this.onChange.bind(this);
	}
	
	toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        })
	}

	handlePhone = e => {
		this.setState({phone: e.target.value});
	}

	handleCheck = (e) => {
		this.setState({checked: !this.state.checked});
	}

	onChange=(e) => {
		let value = e.target.value
		value = value.replace(/[^A-Za-z]/ig, '')
	}

	handleSubmit(values) {
		var moneyval = this.props.moneyVal;
		var dayval = this.props.dayVal;
		var other = {};
		other.loan_amount = moneyval;
		other.period_in_days = dayval;
		other.major_loan_amount = Math.floor(parseInt(moneyval) * 1.15);
		other.grace_period_amount = Math.floor(parseInt(moneyval) * 1.15);
		other.loan_amount_for_max_days =  Math.floor(Math.round(parseInt(moneyval) * (1+(parseInt(dayval)/100)*2))/100*100);
		other.insurance_amount = Math.floor(parseInt(moneyval)*1.15) - parseInt(moneyval);
		other.award_amount = Math.floor(parseInt(moneyval)*1.15) - parseInt(moneyval);
		other.source = 'zaymi.kz';
		const finalObjects = {...other, ...values}

		this.props.postRegistration(finalObjects);
	}
	
	componentDidMount() {
		window.scrollTo(0, 0);
		const progress = document.querySelector('.progress-done');
		progress.style.width = progress.getAttribute('data-done') + '%';
		progress.append(progress.getAttribute('data-done') + "%")
		progress.style.opacity = 1;


		$(document).ready(function(){
			$('input').on('change', function(){
				var cntreq = 0;
				var cntvals = 0;
				$('input').each(function(i, val) {
						cntreq++;
						if($(this).val() != '') {
							cntvals+=5;
						}
				});
				var count = (cntvals/cntreq)*100-20;
				$('#percentage').empty();
				$('#percentage').append(cntvals	+'% completed');

				$('#progress-done').data('done', cntvals)
				$('#progress-done').width($("#progress-done").data('done') + "% ")
				$('#progress-done').text($("#progress-done").data('done') + "% ")
			
			});
		});
	}
	render() {
		return (
			<div>
				<div class="progressBar">
						<div class="progress-done" id="progress-done" data-done="15">
							
						</div>
						
						<p className="counter">Вероятность одобрения</p>
				</div>
			
					{/* <h1 className="percentageFill" id="percentage"></h1> */}
					
				<Form className="container formsStep"  model='registration' onSubmit={(values) => this.handleSubmit(values)}>
	
						{this.props.somemessage.error !== null ?  
							<div class="alert alert-danger" role="alert">
        							<strong> { this.props.somemessage.error }</strong>
            				</div> : null}
							<h2 className="text-center">Регистрация</h2>
							<Row className="form-group mb-3 ">
                                <Label htmlFor='loan_amount'>Сумма:</Label>
                                <div class="input-group ">
                                    <Control.text model='.loan_amount' id='loan_amount' name='loan_amount' placeholder='example@example.com' class="form-control"   value={this.props.moneyVal} disabled
                                        className='form-control focus-visible' data-focus-visible-added
                                    />
									<div className="input-group-append">
                                           <a data-toggle="modal" data-target="#myModalCalc" class="input-group-text" onClick={this.toggleModal}>Изменить...</a>
                                       </div>
                                </div>
							</Row>

							<Row className="form-group mb-3 ">
                                <Label htmlFor='period_in_days'>Дней:</Label>
                                <div class="input-group ">
                                    <Control.text model='.period_in_days' id='period_in_days' name='period_in_days'  class="form-control"   value={this.props.dayVal} disabled
                                        className='form-control'
                                    />
									<div className="input-group-append">
                                           <a data-toggle="modal" data-target="#myModalCalc" class="input-group-text" onClick={this.toggleModal}>Изменить...</a>
                                       </div>
                                </div>
							</Row>
							
						    <Row className="form-group  mb-3 ">
                                <Label htmlFor='name'>Имя * :<span className="cyrrilic"> (только на киррилице)</span> </Label>
                                <div class="input-group ">
									<Control.text model='.name' id='name' name='name' placeholder='Имя'
										onChange={this.onChange} 
                                        className='form-control  text-capitalize registerCyrril'
                                        validators = {{
											required,
											acceptCirrilic
                                        }}  
                                    />
                                </div>
								
								<Errors  className='text-danger' model='.name' show='touched' 
                                        messages={{
											required: 'Поле обязателен для заполнения!',
											acceptCirrilic: "Нужно вводить только на кириллице"
										 }}
                                />
							</Row>

							<Row className="form-group  mb-3 ">
                                <Label htmlFor='middlename'>Фамилия* : <span className="cyrrilic"> (только на киррилице)</span> </Label>
                                <div class="input-group">
                                    <Control.text model='.middlename' id='middlename' name='middlename' placeholder='Фамилия' 
                                        className='form-control text-capitalize registerCyrril'
                                        validators = {{
											required,
											acceptCirrilic
                                        }}  
                                    />
                                </div>
								<Errors  className='text-danger' model='.middlename' show='touched' 
                                        messages={{
                                            required: 'Поле обязателен для заполнения! ',
											acceptCirrilic: "Нужно вводить только на кириллице"
                                         }}
                                />
							</Row>
							<Row className="form-group  mb-3 ">
                                <Label htmlFor='last_name'>Отчество: <span className="cyrrilic"> (только на киррилице)</span>  </Label>
                                <div class="input-group">
                                    <Control.text model='.last_name' id='last_name' name='last_name' placeholder='Отчество' class="form-control" 
                                        className='form-control text-capitalize registerCyrril' validators = {{
											
                                        }}  
                                    />
									<Errors  className='text-danger' model='.last_name' show='touched' 
                                        messages={{
										 }}
                                />
                                </div>
	
							</Row>
							<Row className="form-group  mb-3 ">
                                <Label htmlFor='email'>Электронный адрес * :</Label>
                                <div class="input-group">
                                    <Control.text model='.email' id='email' name='email' placeholder='example@example.com' class="form-control" 
                                        className='form-control'
                                        validators = {{
                                            required, 
											validEmail,
                                        }}  
                                    />
                                </div>
								
								<Errors  className='text-danger' model='.email' show='touched' 
                                        messages={{
                                            required: 'Поле обязателен для заполнения! ',
											validEmail: 'Неправильный email!',
										 }}
										 

                                />
							</Row>
							<Row className="form-group  mb-3 ">
                                <Label htmlFor='iin'>ИИН * : </Label>
							
                 
                                <div class="input-group">
									<Control  name="iin" model=".iin"  class="form-control"  component={IinMask} placeholder="ИИН" validators={{iin}}/>
                                </div>
								<Errors  className='text-danger' model='.iin' show='touched' 
                                        messages={{
											required: "Поле обязателен для заполнения",
                                            iin: 'Неправильный ИИН!',
                                         }}
                                />
							</Row>

							<Row className="form-group mb-3">
								<Label>Номер телефона:</Label>
								<div class="input-group">
								<Control className="form-control" placeholder="+7(705)000-00-00" model=".phone" component={PhoneMask} validators={{ phoneCheck}}/>
								</div>
								<Errors  className='text-danger' model='.phone' show='touched' 
                                        messages={{
											required: "Поле обязателен для заполнения",
                                            phoneCheck: 'Нет соответствующего оператора ',
                                         }}
                                />
							</Row>
					
							<Row className="form-group  mb-3 ">
                                <Label htmlFor='password'>Придумайте пароль * : </Label>
                                <div class="input-group">
                                    <Control.text model='.password' id='password' name='password' placeholder='Пароль' class="form-control" type="password"
                                        className='form-control'
                                        validators = {{
											required,
                                            passwordCheck,
                                        }}
                                    />
                                </div>
								<Errors  className='text-danger' model='.password' show='touched'  
                                        messages={{
											required: "Поле обязателен для заполнения",
											passwordCheck: "Пароль должен быть не меньше 5 символов"
                                         }}
                                />
							</Row>

							<Row className="form-group  mb-3 ">
                                <Label htmlFor='password_confirmation'>Повторите пароль * : </Label>
                                <div class="input-group">
									<Control.text model='.password_confirmation' id='password_confirmation' name='password_confirmation' placeholder='Пароль'
										type="password"
                                        className='form-control'
                                        validators = {{
                                            // confirmPassword: confirmPassword(this.props.registration1 )
                                        }}
                                    />
                                </div>
								<Errors  className='text-danger' model='.password_confirmation' show='touched'  
                                        messages={{
											// confirmPassword: "Пароли не совпадают"
                                         }}
                                />
							</Row>

							<Row className="form-group d-flex align-items-center">
                                   <input type="checkbox" onChange={this.handleCheck} defaultChecked={this.state.checked}/>
                                   <label className="form-check-label ml-3" for="exampleCheck1">Согласен на <a className="agreement " href="https://api.money-men.kz/docs/approve.pdf" target="_blank">обработку данных с условиями сайта</a></label>
							</Row>
										

							{this.props.somemessage.error !== null ? 
							<div class="alert alert-danger" role="alert">
        							<strong> {this.props.somemessage.error.email || this.props.somemessage.error || null}</strong>
            				</div> : null}
							<div className="button form-group" >
								{this.props.loading === true ? 	
								<div>
									
									<Spinner className="loading" size={200} spinnerColor={"#ef2221"} spinnerWidth={2} visible={true} />
									
								</div>
								  :
								 
								<button disabled={!this.state.checked} type="submit " className="agreement-btn">Получить код</button>}
							
							</div>

                        </Form>
						

				<Modal class="modal modal-calculator" isOpen={this.state.isModalOpen} toggle={this.toggleModal}  size="lg">
                 <ModalHeader toggle={this.toggleModal}>Калькулятор</ModalHeader>
				 <ModalBody>
					 <ProgressBar className="progressbar modal" />
					 </ModalBody>
				 </Modal>

			</div>
		)
	}
}

export default (connect(mapStateToProps,mapDispatchToProps)(FormRegister));