import React from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import { Control, Errors, Form,actions } from 'react-redux-form';
import { connect } from 'react-redux';
import {required, validEmail} from '../defaults/validations';
import { addFeedback } from '../redux/actions/ActionCreators';
import InputMask from "react-input-mask";
import Spinner from 'react-spinner-material';
import history from './history';
const mapStateToProps = state => {
  return {
    oplata: state.oplata,
    loading: state.loading,
    somemessage: state.message
  }
}

const mapDispatchToProps = (dispatch) => ({
  addFeedback: dannie => { dispatch(addFeedback(dannie)); },
  resetFeedback: () => { dispatch(actions.reset('oplata'))},
})

const IinMask = (props) => <InputMask mask="999999999999" maskChar=" " className="my-input" {...props} />
const PhoneMask = (props) => <InputMask mask="+7(999)-999-9999" maskChar=" " className="my-input" {...props} />
class Payment extends React.Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleSubmit(values) {
    this.props.addFeedback(values)
  }

  componentDidMount() {
    history.push('/complaint')
  }

  render() {
    return (
        <div>
             <section className="otherPages">
        <div className="">
          <section className="oplata feedback row">
              
            <div className=" oplate--form feedbackForm">
            <h2 className="text-center mb-3">Оставить жалобу</h2>
              <Form model="feedback" className="oplataform" onSubmit={(values) => this.handleSubmit(values)}>
              {this.props.somemessage.success !== null ? 
                    <div class="alert alert-success" role="alert">
                            <strong> {this.props.somemessage.success}</strong>
                    </div> : null
              }
                <div className="input-group">
                  <label htmlFor="name">
                    <h2>Имя:</h2>
                  </label>
                  <Control.text  name="name" model=".name"  placeholder="Вводить сюда" validators={{required}}/>
                  <Errors  className='text-danger' model='.name' show='touched' 
                      messages={{
                        required: "Поле обязателен для заполнения",
					}}
				   />
                </div>
                <div className="input-group">
                  <label htmlFor="email">
                    <h2>Email адрес:</h2>
                  </label>
                  <Control.text  name="email" model=".email"  placeholder="Ваш email адрес" validators={{required,validEmail}}/>
                  <Errors  className='text-danger' model='.email' show='touched' 
                      messages={{
                        required: "Поле обязателен для заполнения",
                       validEmail: "Неправильный email"
					}}
				   />
                </div>
                <div className="input-group">
                    <label htmlFor="phone">
                        <h2>Номер телефона:</h2>
                    </label>
                    <Control placeholder="+7(705)000-00-00" model=".phone" component={PhoneMask} validators={{ required}}/>
                    
                    <Errors  className='text-danger' model='.phone' show='touched' 
                            messages={{
                                required: "Поле обязателен для заполнения",
                                }}
                    />
                </div>
                <div className="input-group">
                  <label htmlFor="feedback">
                    <h2>Сообщения:</h2>
                  </label>
                  <Control.textarea  name="feedback" model=".feedback" type="number" placeholder="Введите сообщение" validators={{required}}/>
                  <Errors  className='text-danger' model='.feedback' show='touched' 
                      messages={{
                       required: 'Заполните все поля ',
						}}/>
                </div>
                <div className="buttonForm">
                {this.props.loading === true ? 	
					<Spinner className="loading" size={200} spinnerColor={"#ef2221"} spinnerWidth={2} visible={true} /> : 
                 <button className=" oplataform--button" type="submit">Отправить</button>}
                </div>
              </Form>
            </div>
          </section>
        </div>
      </section>
        </div>
    );}
}

export default (connect(mapStateToProps,mapDispatchToProps)(Payment));