import React from 'react';
import Helmet from 'react-helmet';

function metaHeadInfo() {
    return(
        <div>
        <Helmet
            title="Деньги в кредит онлайн микрозаймы на карту по Казахстану"
            meta={[
            {"name": "description", "content": "Деньги в кредит, сервис получения микрозаймов онлайн, удобный калькулятор расчета кредита на карту, первые 7 дней без процентов, каждому 100 клиенту займ в подарок"}
            ]}
            link={[
                {"rel": "icon", "href": "./img/svg/zaymi.kz.svg", "sizes": "16x16"},
                {"rel": "apple-touch-icon", "href": "./img/svg/zaymi.kz.svg"}
                
            ]}

        />
        </div>

    )
}

export default metaHeadInfo;