/* eslint-disable react/prop-types */
/* eslint arrow-parens: [2, "as-needed"] */
import React, { Fragment } from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import {Collapse} from 'reactstrap';
import history from '../components/history'
import MetaHead from '../defaults/metaHeadInfo';
import $ from "jquery";
class Nav extends React.Component  {
  constructor(props) {
    super(props);
    this.state = {
        isNavOpen: false,
    };

    this.toggleNav = this.toggleNav.bind(this);
  }

  toggleNav() {
    this.setState ({
        isNavOpen: !this.state.isNavOpen
    });
}

componentDidMount() {
  var $win = $(window);

  $win.scroll(function () {
  if ($(window).width() > 550) {
      if ($win.scrollTop() == 0) {
          $(".navbar").removeClass("fade-color");
      } else if (!$win.scrollTop() == 0) {
          $(".navbar").addClass("fade-color");
      }
    }
  });
}

  render() {
  return (
    <div>
      {/* <MetaHead /> */}
    <nav className=" mb-5 navbar navbar-expand-lg navbar-light  transparent " history={history}>
    <div className="container">
      <NavLink className="navbar-brand logo" to="/">
        <h1>zaymi.kz</h1>
      </NavLink>
      
      <NavLink className="text-center paymentCenter " to="/payment">Внести оплату</NavLink>
      <button  onClick={this.toggleNav} className="navbar-toggler burger" type="button" data-toggle="collapse" data-target="#navigationBar" aria-controls="navigationBar" aria-expanded="false" aria-label="Toggle navigation">
        <div className="animated-icon1"><span /><span /><span /></div>
      </button>
       {/* {this.state.isNavOpen ? <p>opened</p> : <p>closed</p>} */}
      <ul className="phone-numbers">
        <li><i className="fa fa-phone" /><h1><a href="tel:+77272501500">+7 727 <b className="black">250 15 00</b></a></h1></li>
        <li ><i><img  className="whatsapp" src={require("../img/whatsup.svg")} alt="" /></i><h1><a href="tel:+77007501500" target="_blank">+7 700 <b className="black">750 15 00</b></a></h1></li>
      </ul>
      <Collapse navbar isOpen={this.state.isNavOpen} className="text-center justify-content-end" id="navigationBar">
        <ul className="navbar-nav links">
          <li className="nav-item mx-1">
            <NavLink className="nav-link align-items-center d-flex"  to='/sposobi_pogashenia'> Способ
              оплаты</NavLink>
          </li>
          <li className="nav-item mx-1">
            <NavLink className="nav-link align-items-center d-flex font-weight-bold" to="/payment" href="https://raw.githack.com/ansaryergesh/testing-page/master/oplata.html">
              Внести оплату</NavLink>
          </li>
          
          <li className="nav-item mx-1">
            <a className="nav-link align-items-center d-flex" href="https://i-credit.kz/login/"> Личный кабинет</a>
          </li>
        </ul>
      </Collapse>
    </div>
  </nav>
  </div>
  );}
};

export default Nav;